import Vue from 'vue'
import Router from 'vue-router'
//import Home from '@/components/Home'
const Home = r => require.ensure([], () => r(require('@/components/Home')), 'Home');

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    }
  ]
})



